.modal-wrap {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.modal-bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #000;
    opacity: 0.9;
}

.modal {
    position: absolute;
    top: 20%;
    left: 50%;
    background: #fff;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    padding: 50px;
}

@media (max-width: 767px) {
    .modal {
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        -webkit-transform: none;
            -ms-transform: none;
                transform: none;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
    }
}


